<template lang="html">
  <div class="circle-nav-box">
    <div class="circle-nav__head">
      分类导航
    </div>
    <div class="circle-nav__list">
      <div class="circle-nav__item" @click="$router.push('/nearshop')">
        <img src="../../assets/img/circle/shop@3x.png" alt="">
        <div class="">附近实体店</div>
      </div>
      <div class="circle-nav__item" @click="$router.push('/businesscircle')">
        <img src="../../assets/img/circle/boss@3x.png" alt="">
        <div class="">商业圈</div>
      </div>
      <div class="circle-nav__item" @click="$router.push('/friendcircle')">
        <img src="../../assets/img/circle/frs@3x.png" alt="">
        <div class="">生意伙伴</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
.circle-nav-box {
  color: @defaultColor;
  background: @whiteColor;

  .circle-nav__head {
    height: .88rem;
    line-height: .88rem;
    font-size: .32rem;
    margin: 0 .22rem;
    // border-bottom: 1px solid @lineColor;
  }
  .circle-nav__list {
    height: 1.6rem;
    display: table;
    table-layout: fixed;
    width: 100%;
    text-align: center;
  }
  .circle-nav__item {
    font-size: .28rem;
    display: table-cell;
    vertical-align: middle;
    img {
      width: .66rem;
      height: .60rem;
      margin-bottom: .1rem;
    }
  }
}
</style>



