<template lang="html">
  <div class="shop-item-box">
    <img class="goods-pic" src="http://img.zcool.cn/community/0109815595e02a32f87598b5a397d4.jpg@900w_1l_2o_100sh.jpg" alt="">
    <div class="shop-item__info">
      <div class="shop-name">这里是店铺名称</div>
      <star-line width="50%"></star-line>
      <div class="shop-address-info">
        <div class="l">饭店 | 体育西路店</div>
        <div class="r">
          <img src="../../assets/img/ad@2x.png" alt="">
          <span>106m</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import starLine from '../common/starLine'
export default {
  components: {starLine}
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
.shop-item-box {
  background: @whiteColor;
  padding: .2rem .22rem;
  border-bottom: 1px solid @lineColor;
  overflow: hidden;
  position: relative;
  .goods-pic {
    float: left;
    width: 2rem;
    height: 2rem;
    margin-right: .2rem;
  }
  .shop-item__info {
    overflow: hidden;
    .shop-name {
      margin-bottom: .1rem;
      font-size: .3rem;
      color: @defaultColor;
    }
    .shop-address-info {
      margin-top: .80rem;
      overflow: hidden;
      color: @lightColor;
      font-size: .28rem;
      .l {
        float: left;
      }
      .r {
        float: right;
        img {
          width:.46rem;
          height:.46rem;
        }
        img, span {
          vertical-align: middle;
        }
      }
    }
  }
}
.shop-item-box:last-child {
  border-bottom: none;
}
</style>



