<template lang="html">
  <div class="search-box">
    <i class="search__icon circle-search__icon"></i>
    <input disabled="true" class="search__input" type="text" name="" value="电影票">
  </div>
</template>

<script>
export default {
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
.search-box {
  background: @whiteColor;
  border-bottom: 1px solid @lineColor;
  font-size: .24rem;
  color: @lightColor;
  position: relative;
  padding: .1rem .22rem;
  i {
    top: 50%;
    left: .28rem;
    transform: translateY(-50%);
  }
  .search__input {
    width: 100%;
    border: none;
    height: .68rem;
    background: @inputbgColor;
    text-indent: .6rem;
    color: @lightColor;
  }
}
</style>



