<template lang="html">
  <div class="">
    <default-head :hasArrow="false" header="圈子">
      <img v-if="false" class="cirle-sao-icon" slot="head-right" src="../assets/img/circle/sao@3x.png" alt="">
    </default-head>
    <div class="" id="wrapper">
      <search-input v-if="false"></search-input>
      <category-nav></category-nav>
    </div>
    <div class="near-shop-box">
      <div class="near-shop__title">
        附近推荐
      </div>
      <div class="near-shop__list">
        <no-data></no-data>
        <shop-item v-if="false"></shop-item>
      </div>
    </div>
    <tabbar selected="/circleindex"></tabbar>
  </div>
</template>

<script>
import noData from '@/components/common/noData'
import tabbar from '@/components/minCommon/tabbar'
import defaultHead from '@/components/head/defaultHead'
import searchInput from '@/components/circleIndex/search'
import categoryNav from '@/components/circleIndex/categoryNav'
import shopItem from '@/components/circleIndex/shopItem'
export default {
  components: {noData, defaultHead, searchInput, categoryNav, shopItem, tabbar}
}
</script>

<style lang="less">
@import '../assets/css/variable.less';

.cirle-sao-icon {
  width: .46rem;
  height: .46rem;
}
.near-shop-box {
  background:@whiteColor;
  margin-top: .3rem;
  .near-shop__title {
    font-size: .32rem;
    color: @defaultColor;
    height: .88rem;
    line-height: .88rem;
    margin: 0 .22rem;
    border-bottom: 1px solid @lineColor;
  }
  .near-shop__list {
    width: 100%;
    height: 6.8rem;
    position: relative;
  }
}
</style>



